@use "base/mixins" as m;
@use "base/variables" as v;

.destinos {
  .destino {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }

  &__grid {
    @include m.grid(1, rem);

    @include m.tablet {
      @include m.grid(2, 2rem);
    }

    @include m.desktop {
      @include m.grid(4, 2rem);
    }
  }

  &__imagen {
    width: 14rem;
    height: 14rem;
  }

  &__nombre,
  &__distancia {
    margin: 0;
  }
}
